// 3.     ,     .
//        "Sorry, there are no images matching your search query. Please try again.".
//  4.           ,    .
//  5. Pixabay API      page  per_page.
//   ,      40 (  20).
//  5.1    page   1.
//  5.2    ,     1.
//  5.3        page    .
//  6.5          : "We're sorry, but you've reached the end of search results.".
