// import axios from 'axios';
import Notiflix from 'notiflix';
import { debounce } from 'lodash';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import { ImgApiService } from './search-img-service';

const searchForm = document.querySelector('#search-form');
const galleryBox = document.querySelector('.gallery');
const guard = document.querySelector('.js-guard');
const imgApiService = new ImgApiService();
const observOptions = {
  root: null,
  rootMargin: '300px',
  threshold: 0,
};
let observer = new IntersectionObserver(onLoadMore, observOptions);
let totalHits = '';

searchForm.addEventListener('submit', onSearchClick);

function onLoadMore(entries, observer) {
  entries.forEach(entry => {
    if (entry.isIntersecting) {
      imgApiService.incrementPage();
      imgApiService
        .pixabayApi()
        .then(data => {
          createImgMarkup(data.hits);
          if (
            imgApiService.page ===
            Math.round(data.totalHits / imgApiService.perPage)
          ) {
            observer.unobserve(guard);
            ImageIsOverMassege();
          }
        })
        .catch(err => console.log(err.message));
    }
  });
}

function createImgMarkup(arr) {
  const markup = arr
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) =>
        `
    <div class="photo-card">
    <a class="gallery__item" href="${largeImageURL}">
    <img class="img" src="${webformatURL}" alt="${tags}" loading="lazy" /></a>
    <div class="info">
    <ul class="info-list list">
      <li class="info__item">
        <p><b>Likes</b></p>
        <p>${likes}</p>
      </li>
      <li class="info__item">
        <p><b>Views</b></p>
        <p>${views}</p>
      </li>
      <li class="info__item">
        <p><b>Comments</b></p>
        <p>${comments}</p>
      </li>
      <li class="info__item">
        <p><b>Downloads</b></p>
        <p>${downloads}</p>
      </li>
    </ul>
  </div>
  </div>`
    )
    .join('');

  galleryBox.insertAdjacentHTML('beforeend', markup);
  summonSimpleLightbox();
}

function summonSimpleLightbox() {
  const options = {
    // captions: true,
    captionsData: 'alt',
    captionDelay: 250,
  };
  const lightbox = new SimpleLightbox('.gallery a', options);
  lightbox.refresh();
}

function firstSearchMassege(totalHits) {
  if (totalHits) {
    Notiflix.Notify.success(`Hooray! We found ${totalHits} images.`);
  }
}

function resetMarkup() {
  galleryBox.innerHTML = '';
}

function onSearchClick(evt) {
  evt.preventDefault();
  imgApiService.quary = searchForm.searchQuery.value.trim();
  imgApiService.resetPage();
  resetMarkup();

  imgApiService
    .pixabayApi()
    .then(data => {
      createImgMarkup(data.hits);
      observer.observe(guard);
      totalHits = data.totalHits;
      firstSearchMassege(totalHits);
    })
    .catch(err => console.log(err.message));
}

function ImageIsOverMassege() {
  Notiflix.Notify.info(
    "We're sorry, but you've reached the end of search results."
  );
}
